.navbar{
    padding:20px 0;
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.navabar .logo{
    width:150px;
}
.navbar-menu{
    display:flex;
    list-style:none;
    gap:20px;
    color:#49557e;
    font-size:18px;
}
.navbar-right{
    display:flex;
    align-items:center;
    gap:40px;
}
.navbar button{
    background: transparent;
    font-size: 16px;
    color:#49557e;
    border:1px solid tomato;
    padding:10px 30px;
    border-radius: 50px;
    cursor:pointer;

}
.navbar button:hover{
    background-color: #fff4f2;
}
.navbar .active{
    padding-bottom: 2px;
    border-bottom: 2px solid #49557e;
}
.navbar li{
    cursor:pointer;
}
.navbar-search-icon{
    position: relative;

}
.navbar-search-icon .dot{
    position :absolute;
    min-width:10px;
    min-height:10px;
    background-color: tomato;
    border-radius: 5px;
    top:-8px;
    right:-8px;


}



