.food-item{
    width:100%;
    margin:auto;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #00000015;
    transition:0.3s;
    animation:fadeIn 3s;
}
.food-item-image{
    width:100%;
    border-radius: 35px 35px 0px 0px;
    
    
}
.food-item-image img{
    width:300px;
    
    border-radius: 15px 15px 0px 0px;
   
}
.food-item-info{
    padding:20px;
}
.food-item-name-rating{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.food-item-name-rating p{
    font-size: 20px;
    font-weight: 500;
}
.food-item-name-rating img{
    width:70px;
}
.food-item-price{
    color:tomato;
    font-weight:500;
    font-size: 22px;
    margin:10px 
}
.food-item-img-container{
    position:relative;

}
.food-item-img-container .add{
    width:35px;
    position: absolute;
    bottom:15px;
    right:15px;
    cursor:pointer;
    border-radius: 15px;;

}.food-item-counter{
    position:absolute;
    bottom:15px;
    right:15px;
    display:flex;
    align-items:center;
    gap:10px;
    padding:6px;
    border-radius: 50px;
    background-color: white;
}
