.header {
    height: 34vw;
    margin: 30px auto;
    background: url('/public/header_img.png') no-repeat center center;
    background-size: contain;
    position:relative;
  }
  
  .header-content{
    position :absolute;
    display:flex;
    flex-direction: column;
    align-items: start;
    gap:1.5vw;
     max-width: 50%;
     bottom:10%;
     left:6vw;
     animation:fadeIn 3s ;
  }
  .header-content h2{
    font-weight: 500;
    color:white;
    font-size:max(4.5vw,22px);

  }
  .header-content p{
   color:white;
   font-size: 1.5vw;
    
  }
  .header-content button{
    border:none;
    color:#747474;
    font-weight: 500;
    padding:1vw 2.3vw;
    background-color: white;
    font-size: max(1vw,13px);
    border-radius: 50px;
   }